html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6,
span,
div,
p,
small {
  font-family: 'Roboto', sans-serif;
}
.Block {
  box-sizing: border-box;
  overflow: hidden;
  padding: 1rem;
  width: 100%;
}
.Block__content {
  word-wrap: break-word;
  overflow: hidden;
  line-height: 1.5rem;
  font-weight: 300;
}
.Block__content--center {
  text-align: center;
}
.Block:after {
  display: block;
  content: "";
  height: 0;
  visibility: hidden;
}
.Block--dark {
  background-color: rgba(255,255,255,0.2);
}
.Block--dark__content {
  color: #fffbf2;
}
.Block--dark-highlight {
  background-color: #e69900;
}
.Block--dark-highlight__content {
  color: #fffbf2;
}
.Block--light {
  background-color: rgba(0,0,0,0.2);
}
.Block--light__content {
  color: #0d0900;
}
.Block--light-highlight {
  background-color: #ffb31a;
}
.Block--light-highlight__content {
  color: #0d0900;
}
.Button {
  transition: all 100ms ease-in-out;
  display: block;
  padding: 1rem 1rem;
  text-align: center;
  font-size: 1.5rem;
}
.Button:hover {
  transition: all 100ms ease-in-out;
}
.Button--primary {
  background-color: #fa0;
  color: #fffbf2;
  border: 1px solid rgba(0,0,0,0.2);
  text-decoration: none;
}
.Button--primary:hover {
  background-color: #fffbf2;
  color: #fa0;
  border-color: #fa0;
}
.Button-- {
  width: 100%;
}
.Card {
  overflow: hidden;
  padding: 1rem;
  width: 100%;
}
.Card__header {
  padding: 1rem;
}
.Card__title {
  font-weight: 600;
  font-size: 2rem;
}
.Card__body {
  padding: 1rem 0;
}
.Card__list {
  list-style: none;
}
.Card__list-item {
  list-style: none;
  padding: 1rem;
}
.Card__list-item-title {
  font-size: 1.5rem;
}
.Card__list-item-content {
  margin-top: 0.5rem;
}
.Card--light {
  background-color: rgba(255,255,255,0.2);
  border: 1px solid rgba(0,0,0,0.2);
}
.Card--light__header {
  border-bottom: 1px solid #ffb31a;
}
.Card--light__title {
  color: #0d0900;
}
.Card--light__list-item-title {
  color: #0d0900;
}
.Card--dark {
  background-color: rgba(0,0,0,0.2);
  border: 1px solid rgba(255,255,255,0.2);
}
.Card--dark__header {
  border-bottom: 1px solid #e69900;
}
.Card--dark__title {
  color: #fffbf2;
}
.Card--dark__list-item-title {
  color: #fffbf2;
}
.Code {
  font-family: Consolas, monospace;
  padding: 0.5rem;
  white-space: pre;
  line-height: 1.5rem;
  overflow: auto;
  word-wrap: normal;
}
.Code--light {
  background-color: rgba(0,0,0,0.05);
  border: 1px solid rgba(0,0,0,0.2);
  color: #0d0900;
}
.Code--dark {
  background-color: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.2);
  color: #fffbf2;
}
.Footer {
  padding: 1rem 0;
}
.Footer--light {
  background-color: #fffbf2;
}
.Footer--dark {
  background-color: #0d0900;
}
.Jumbotron {
  padding: 4rem 0;
}
.Jumbotron__header {
  padding: 2rem 0;
}
.Jumbotron__title {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 5rem;
  font-weight: 700;
}
.Jumbotron__subtitle {
  font-weight: 100;
  font-size: 2.5rem;
  display: block;
  text-align: center;
}
.Jumbotron__underline {
  width: 200px;
  height: 2px;
  display: block;
  margin: 2.5rem auto;
}
.Jumbotron__body {
  padding: 2rem 0;
}
.Jumbotron--dark {
